import React, { Component } from "react";
import kodeBarang from "../Images/kode_barang.svg";
import kelompok from "../Images/kelompok.svg";
import kategori from "../Images/kategori.svg";
import { Link } from "react-router-dom";

class SaldoAwal extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="container-fluid" style={{ width: "100%" }}>
        <div className="row col-lg-12">
          <div className="col-lg-3">
            <Link to="/SaldoAwalHutang">
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={kelompok} width="100%" alt="Kode Model" />
                  <h3 style={{ color: "#000" }}>Hutang</h3>
                  <button className="btn btn-primary">Masuk</button>
                </div>
              </div>
            </Link>
          </div>
          <div className="col-lg-3">
            <Link to="/saldoAwalPiutangCustomer">
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={kategori} width="100%" alt="Kategori" />
                  <h3 style={{ color: "#000" }}>Piutang Customer</h3>

                  <button className="btn btn-primary">Masuk</button>
                </div>
              </div>
            </Link>
          </div>
          <div className="col-lg-3">
            <Link to="/saldoAwalStock">
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={kodeBarang} width="100%" alt="Kode Model" />
                  <h3 style={{ color: "#000" }}>Stock</h3>
                  <button className="btn btn-primary">Masuk</button>
                </div>
              </div>
            </Link>
          </div>
          <div className="col-lg-3">
            <Link to="/saldoAwalCIOK">
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={kodeBarang} width="100%" alt="Kode Model" />
                  <h3 style={{ color: "#000" }}>CT/CK</h3>
                  <button className="btn btn-primary">Masuk</button>
                </div>
              </div>
            </Link>
          </div>
        </div>
      </div>
    );
  }
}

export default SaldoAwal;
